        <div class="modal-content" :style="modalContentClass">
          <div class="modal-header border-0">
            <h5 class="modal-title" id="exampleModalLongTitle">
              <slot name="modalTitle">Slot: modalTitle</slot>
            </h5>

            <!-- Close Button -->
            <button type="button" class="close text-danger" data-dismiss="modal" aria-label="Close" @click="closeModalClick">
              <span aria-hidden="true">&times;</span>
            </button>
            <!-- / Close Button -->

          </div>
          <div class="modal-body" :style="modalBodyClass">
            <slot name="modalBody">
              <terms />
            </slot>
          </div>
          <div class="modal-footer border-0" v-if="!noCloseButton">
            <button type="button" class="btn btn-danger" data-dismiss="modal" @click="closeModalClick">Close</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ModalComponent",
  props: {
    buttonClass: {
      type: String,
      default() {
        return "btn";
      }
    },
    buttonText: {
      type: String,
      default: "Please read the Terms & Conditions (buttonText)"
    },
    modalTitle: {
      type: String,
      default: "Modal Title here(prop: modalTitle)"
