    modalBodyClass: {
      type: String,
      default: "text-dark"
    },
    modalContentClass: {
      type: Object,
      default: () => {}
    },
    showModal: {
      type: Boolean,
      default: true
    },
    modalParentStyle: {
      type: Object,
      default: () => {}
    }, 
    noCloseButton : {
      type: Boolean,
      default: false
    },
    dataTarget: {
      type: String,
      default: 'exampleModalLong'
    }
  },
  components: {
    Terms: () => import("../utils/TandC")
  },
  methods: {
    atClicked() {
      this.showModal = true;
      this.$emit("atClicked");
    },
    closeModalClick () {
      // eslint-disable-next-line
      console.log('Modal closed ')
      this.$emit("closeModalClick");
      // this.showModal = false;
    },
    closeModal() {
      this.showModal = false;
      // return this.$('#exampleModalLong').modal('hide')
    }
  },
  watch: {
    showModal(value) {
      return value;
    }
  },
  created() {
    // this.$('#exampleModalLong').modal('hide')
  }
};
</script>

